<template>
  <section class="swipe-wrap">
    <mt-swipe :auto="4000">
      <mt-swipe-item
        v-for="item in swipe"
        :key="item.ad_id"
      >
        <div class="img-wrap">
          <img :src="item.imgurl" alt="">
        </div>
      </mt-swipe-item>
    </mt-swipe>
  </section>
</template>

<script>
  export default {
    props: {
      swipe: Array
    }
  }
</script>

<style lang="less" scoped>
  .swipe-wrap{
    width: 100%;
    height: 206px;
    position: relative;
    img {
      width: 100%;
      height: 206px;
    }
  }
</style>



