<template>
  <div class="ui-wrap shop ios-s">
    <shop-header
      :logo="ad[105]"
      :header-img="ad[104]"
      :shop="shopInfo"
    />
    <shop-nav
      :shop-id="shopId"
    />
    <shop-swipe
      :swipe="ad[102]"
    />
    <section class="goods-list">
      <div class="title">
        <h3>店铺推荐</h3>
      </div>
      <div class="list clearfix">
        <mall-item
          v-for="item in list"
          :key="item.id"
          :goods="item"
        />
      </div>
    </section>
  </div>
</template>

<script>
  import ShopHeader from './shop-header'
  import ShopNav from './shop-nav'
  import ShopSwipe from './shop-swipe'
  import MallItem from '../mall/mall-item'
  import {ShopUrl} from '../../config/urls'

  export default {
    data () {
      return {
        ad: [],
        shopId: '',
        shopInfo: '',
        list: []
      }
    },
    created () {
      this.getShopInfo()
      this.getData()
    },
    methods: {
      getData () {
        this.shopId = this.$route.params.id
        this.$http.get(ShopUrl.ad.list, {params: {unit_id: this.$route.params.id}})
          .then(data => {
            this.ad = data.data.data.list
          })
          .catch(err => this.$toast('网络错误:' + err.message))

        this.$http.get(ShopUrl.goods.suggest, {params: {unit_id: this.$route.params.id}})
          .then(data => {
            this.list = data.data.data.list
          })
          .catch(err => this.$toast('网络错误:' + err.message))
      },
      getShopInfo () {
        this.$http.get(ShopUrl.info, {params: {unit_id: this.$route.params.id}})
          .then(data => {
            let res = data.data
            if (res.state) {
              this.shopInfo = res.data
            } else {
              this.$toast(res.errormsg)
              setTimeout(() => {
                this.$router.push('/')
              }, 1500)
            }
          }).catch(err => this.$toast(err.message))
      }
    },
    components: {
      ShopHeader,
      ShopNav,
      ShopSwipe,
      MallItem
    }
  }
</script>

<style lang="less" scoped>
  .shop{
    min-height: 100%;
    padding-top: 0 !important;
    padding-bottom: 100px;
    background-color: #fff!important;
    overflow-y: scroll;
  }
  .goods-list{
    position: relative;
    .list{
      padding: 0 .5rem;
    }
    .title{
      background: #fafafa;
      padding: 6px 15px;
      color: #777;
      border-color: #ddd;
      border-bottom: 1px solid transparent;
      border-top-left-radius: 3px;
      border-top-right-radius: 3px;
      h3{
        font-size: 16px;
        margin: 0;
        padding: .2rem;
      }
    }
  }
</style>



