<template>
  <router-link :to="'/mall/item/' + goods.mall_id" class="item-wrap">
    <div class="img">
      <img v-lazy="goods.imgurl || '/static/img/default-bg.jpg'">
    </div>
    <div class="info">
      <h3 class="name">{{goods.name}}</h3>
    </div>
  </router-link>
</template>

<script>
export default {
  name: 'Mall-Item',
  props: {
    goods: ''
  }
}
</script>

<style lang="less" scoped>
  .item-wrap{
    display: block;
    float: left;
    width: 49%;
    margin-right: .2rem;
    color: #333;
    &:nth-child(2){
      margin-right: 0;
    }
    .img{
      img {
        width: 100%;
        height:100%;
        max-height: 132px;
      }
    }
    .info{
      padding: .2rem .27rem;
      .name{
        margin: 0;
        font-size: 1rem;
        font-weight: 500;
        color: rgba(0,0,0,.87);
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
</style>



