<template>
  <section class="mp-header">
    <div class="tm-shop-header">
      <div class="shop-back-img">
        <img :src="headerImg[0].imgurl" alt="">
      </div>
      <div class="tm-shop-header-info">
        <a href="javascript:void(0)" class="shop-logo">
          <img :src="logo[0].imgurl" alt="">
        </a>
        <div class="contact">
          <div class="ctn">
            <span>{{shop.info}}</span>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  export default {
    props: {
      logo: [Object, Array],
      headerImg: [Object, Array],
      shop: Object
    }
  }
</script>

<style lang="less" scoped>
  .mp-header{
    position: relative;
    .tm-shop-header{
      position: relative;
      width: 100%;
      height: 118px;
      background: rgba(0,0,0,.25);
      overflow: hidden;
      .shop-back-img{
        &:before{
          content: ' ';
          background-color: rgba(0,0,0,.4);
          width: 100%;
          height: 100%;
          position: absolute;
          left: 0;
          z-index: 1;
          top: 0;
        }
        img{
          position: absolute;
          height: 100%;
          width: 100%;
          top: 50%;
          left: 50%;
          transform: translate3d(-50%, -50%, 0);
        }
      }
    }
  }
  .tm-shop-header-info{
    z-index: 99;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    .shop-logo{
      width: 66px;
      height: 66px;
      position: relative;
      z-index: 9;
      float: left;
    }
  }
  .contact{
    position: relative;
    height: 47px;
    margin-left: 75px;
    margin-top: 7px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    .ctn{
      display: inline-block;
      height: 27px;
      font-size: 14px;
      color: #fff;
      line-height: 27px;
      width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
</style>



