<template>
  <section class="shop-nav">
    <div class="s-nav">
      <router-link :to="`/shop/${shopId}`">
        <i class="fa fa-globe" aria-hidden="true"></i>
        <span>首页</span>
      </router-link>
      <router-link :to="`/shop/${shopId}/search`">
        <i class="fa fa-shopping-bag" aria-hidden="true"></i>
        <span>全部商品</span>
      </router-link>
      <router-link :to="`/shop/${shopId}/search?`">
        <i class="fa fa-gg" aria-hidden="true"></i>
        <span>新品</span>
      </router-link>
      <!--<a href="javascript:void(0)">-->
        <!--<i class="fa fa-apple" aria-hidden="true"></i>-->
        <!--<span>其他</span>-->
      <!--</a>-->
    </div>
  </section>
</template>

<script>
  export default {
    props: {
      shopId: [String, Object]
    }
  }
</script>

<style lang="less" scoped>
  .shop-nav{
    position: relative;
    z-index: 99;
    .s-nav {
      position: relative;
      width: 100%;
      height: 62px;
      z-index: 9999;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      box-shadow: 0 1px 1px rgba(0,0,0,.2);
      display: flex;
      background-color: #fff;
      border-top: 1px solid rgba(0,0,0,.1);
      border-bottom: 1px solid rgba(0,0,0,.1);
      a {
        flex: 1;
        height: 62px;
        position: relative;
        text-align: center;
        color: #051B28;
        i {
          position: absolute;
          left: 50%;
          -webkit-transform: translate(-50%,0);
          transform: translate(-50%,0);
          top: 10px;
          font-size: 20px;
        }
        span{
          display: inline-block;
          font-size: 14px;
          line-height: 14px;
          margin-top: 37px
        }
      }
    }
  }

  .active {
    color: #DD2727 !important;
    &:after{
      content: "";
      position: absolute;
      width: 100%;
      left: 0;
      bottom: 0;
      height: 3px;
      background: #DD2727;
    }
  }
</style>



